<template>
    <h2>단어장 ( 표준어 / 문화어 )</h2>
    <button @click="cates(i)" v-for="i in (0, 4)" :key="i" class="cat-button">
        {{ button_list[i - 1] }}
    </button>
    <div v-for="(a, i) in words_new" :key="i">
        <div class="words_flex">
            <p @click="move(words_new[i].Sth)">{{ words_new[i].Sth }}</p>
            <p @click="move(words_new[i].Nth)">{{ words_new[i].Nth }}</p>
        </div>
    </div>
</template>

<script>
import words from "../assets/words";

export default {
    name: 'App',
    data() {
        return {
            cate: '',
            words,
            words_new: [],
            repeat: 0,
            button_list: ["생활", "동식물", "음식", "운동"],
        }
    },
    methods: {
        getCatWords(cats) {
            for (var i = 0; i < this.words.length; i++) {
                if (this.words[i].cat == cats) {
                    this.words_new.push(this.words[i])
                }
            }
        },
        move(voca) {
            window.open("https://ko.dict.naver.com/#/search?query=" + voca)
        },
        async cates(num) {
            if (this.repeat >= 1) {
                this.words_new = []
            }
            console.log(num - 1)
            this.getCatWords(num - 1);
            this.repeat++
        }
    },
}
</script>

